.form-section {
	.form-wrapper {
		padding-left: 62px;
		.btn {
			margin-top: 7px;
		}
		.form-group {
			margin-bottom: 33px;
			position: relative;
		}
		.form-control {
			background-color: $black-lighter-clr;
			border: 1px solid $black-lighter-clr;
			min-height: 56px;
			border-radius: 4px;
			resize: none;
			font-size: 16px;
			line-height: 27px;
			color: $white-clr;
			padding: 15px 16px 14px;
			&:invalid {
				border: 1px solid $red-clr;
			}
			&:required {
				border: 1px solid $black-lighter-clr;
			}
			&::placeholder {
				color: #737373;
			}
			&:focus {
				box-shadow: 0 0 0 .2rem #28B3A145;
			}
		}
		[class*="col-"] {
			padding-left: 8px;
			padding-right: 8px;
		}
	}
	.form-error {
		font-size: 13px;
		color: $red-clr;
		position: absolute;
		top: 100%;
		height: 27px;
		display: flex;
		align-items: center;
		left: 1rem;
	}
	.form-success {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		left: 62px;
		background-color: $black-clr;
		background-image: url("data:image/svg+xml,%3Csvg width='129' height='92' viewBox='0 0 129 92' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M48.3741 70.3565L118.244 0L129 10.8217L48.3741 92L0 43.2946L10.7481 32.4728L48.3741 70.3565Z' fill='%2328B3A1'/%3E%3C/svg%3E%0A");
		background-position: center center;
		background-size: 129px;
		background-repeat: no-repeat;
		display: flex;
		flex-direction: column;
	}
}